import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import EditingDesk from '../views/EditingDesk.vue'
import Explore from '../views/Explore.vue'
import Login from '../views/Login.vue'




Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home
  },
  {
    path: '/Explore',
    name: 'Explore',
    component: Explore
  },
  {
    path: '/EditingDesk',
    name: 'EditingDesk',
    component: EditingDesk
  },
  {
    path: '/Login',
    name: 'Login',
    component: Login
  },



]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
